.header {
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: var(--primary-color);
}
@media (max-width: 1200px) {
  .header {
    height: 90px;
  }
}
@media (max-width: 990px) {
  .header {
    height: 70px;
  }
}
.header__inner {
  display: flex;
  justify-content: space-between;
}
.header__logo {
  margin-right: 20px;
  display: flex;
}
.header__wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
@media (max-width: 990px) {
  .header__wrapper {
    display: none;
  }
}
.header__location {
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: var(--text-color-light);
  padding: 5px;
  margin-bottom: 8px;
  cursor: pointer;
}
@media (max-width: 1200px) {
  .header__location {
    font-size: 12px;
    margin-bottom: 3px;
  }
}
.header__location.arrow {
  position: relative;
  padding-right: 20px;
}
@media (max-width: 1200px) {
  .header__location.arrow {
    padding-right: 15px;
  }
}
.header__location.arrow:after {
  content: "";
  position: absolute;
  background-image: url("../assets/img/small-down-arrow.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 10px;
  height: 10px;
  right: 5px;
  top: 12px;
}
@media (max-width: 1200px) {
  .header__location.arrow:after {
    background-image: url("../assets/img/small-down-arrow-1200px.svg");
    width: 5px;
    height: 5px;
  }
}
.header .cities {
  display: none;
}
.header .nav {
  margin-right: 20px;
}
.header .nav ul {
  display: flex;
  justify-content: space-between;
}
.header .nav li:not(:last-child) {
  margin-right: 20px;
}
.header .nav__link {
  padding: 5px;
  font-weight: 700;
  font-size: 16px;
  line-height: 140%;
  letter-spacing: 0.02em;
  white-space: nowrap;
  color: var(--text-color-light);
}
@media (max-width: 1200px) {
  .header .nav__link {
    font-size: 12px;
  }
}
.header .nav .arrow {
  position: relative;
  padding-right: 20px;
}
@media (max-width: 1200px) {
  .header .nav .arrow {
    padding-right: 15px;
  }
}
.header .nav .arrow:after {
  content: "";
  position: absolute;
  background-image: url("../assets/img/small-down-arrow.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 10px;
  height: 10px;
  right: 5px;
  top: 12px;
}
@media (max-width: 1200px) {
  .header .nav .arrow:after {
    background-image: url("../assets/img/small-down-arrow-1200px.svg");
    width: 5px;
    height: 5px;
  }
}
.header__icons {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: -5px;
}
.header__icon {
  display: flex;
  align-items: center;
}
.header__icon:not(:last-child) {
  margin-right: 10px;
}
.header__burger {
  position: relative;
  width: 60px;
  height: 36px;
  padding: 5px;
}
@media (max-width: 1200px) {
  .header__burger {
    width: 41px;
    height: 26px;
  }
}
.header__burger span {
  content: "";
  position: absolute;
  width: 50px;
  height: 2px;
  background-color: #fff;
}
@media (max-width: 1200px) {
  .header__burger span {
    width: 31px;
    height: 1px;
  }
}
@media (max-width: 990px) {
  .header__burger span {
    width: 30px;
  }
}
.header__burger span:before {
  content: "";
  position: absolute;
  width: 50px;
  height: 2px;
  background-color: #fff;
  top: 10px;
}
@media (max-width: 1200px) {
  .header__burger span:before {
    width: 31px;
    height: 1px;
    top: 8px;
  }
}
.header__burger span:after {
  content: "";
  position: absolute;
  width: 50px;
  height: 2px;
  background-color: #fff;
  bottom: 10px;
}
@media (max-width: 1200px) {
  .header__burger span:after {
    width: 31px;
    height: 1px;
    bottom: 8px;
  }
}

@media (max-width: 990px) {
  .header__center {
    position: absolute;
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
  }
  .header__wrapper {
    position: relative;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    background-color: var(--primary-color);
    width: 100%;
    height: 100%;
    z-index: 1000;
  }
  .header__location {
    font-size: 11px;
    height: 60px;
    display: flex;
    align-items: center;
    margin: 0 20px;
    padding: 0;
    width: 100%;
  }
  .header__location.arrow:after {
    top: 28px;
  }
  .header .cities__mask {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1002;
    backdrop-filter: blur(5px);
  }
  .header .cities {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1003;
    background-color: #ccc;
  }
  .header .cities__list {
    margin: 40px 20px;
    background-color: #fff;
  }
  .header .cities li {
    font-weight: 700;
    font-size: 11px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #262728;
    height: 30px;
    display: flex;
    align-items: center;
  }
  .header .nav {
    border-top: 1px solid #000;
    margin: 0;
    padding: 10px 0;
    width: 100%;
  }
  .header .nav ul {
    flex-direction: column;
    align-items: center;
  }
  .header .nav li:not(:last-child) {
    margin: 0 0 3px 0;
  }
  .header .nav__link {
    font-size: 11px;
    line-height: 140%;
  }
}